"use client";

import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent } from "@/components/ui/card";
import { useEffect } from "react";

export default function SubQueryForm({
  category,
  subQueries,
  setSubQueries,
  setFinalQuery,
}: {
  category: string;
  subQueries: any[];
  setSubQueries: any;
  setFinalQuery: any;
}) {
  useEffect(() => {
    setSubQueries([{ issue: "", speed: "", notes: "" }]);
  }, [category]);

  const update = (index: number, field: string, value: string) => {
    const updated = [...subQueries];
    updated[index][field] = value;
    setSubQueries(updated);

    // Build final query string
    const q = updated
      .map(
        (s) =>
          `${category} issue: ${s.issue}, speed: ${s.speed}, notes: ${s.notes}`
      )
      .join("; ");

    setFinalQuery(q);
  };

  return (
    <div className="space-y-3">
      <h2 className="text-xl font-semibold">2. Describe the Issue</h2>

      {subQueries.map((s, i) => (
        <Card key={i} className="border">
          <CardContent className="grid grid-cols-1 md:grid-cols-3 gap-4 py-4">
            <Input
              placeholder="Issue type (e.g., faded, damaged, missing)"
              value={s.issue}
              onChange={(e) => update(i, "issue", e.target.value)}
            />

            <Input
              placeholder="Speed (e.g., 60 km/h)"
              value={s.speed}
              onChange={(e) => update(i, "speed", e.target.value)}
            />

            <Textarea
              placeholder="Additional notes"
              value={s.notes}
              onChange={(e) => update(i, "notes", e.target.value)}
            />
          </CardContent>
        </Card>
      ))}
    </div>
  );
}
