import { Card, CardContent } from "@/components/ui/card";
import { AlertTriangle, Type, Map, ShieldCheck } from "lucide-react";

const categories = [
  { id: "road_sign", label: "Road Sign", icon: ShieldCheck },
  { id: "road_marking", label: "Road Marking", icon: Type },
  { id: "road_hazard", label: "Hazard / Furniture", icon: AlertTriangle },
  { id: "vru", label: "VRU / Pedestrian", icon: Map },
];

export default function CategorySelector({
  setCategory,
}: {
  setCategory: (v: string) => void;
}) {
  return (
    <div className="space-y-3">
      <h2 className="text-xl font-semibold">1. Select Infrastructure Type</h2>

      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        {categories.map((c) => {
          const Icon = c.icon;
          return (
            <Card
              key={c.id}
              onClick={() => setCategory(c.id)}
              className="cursor-pointer border hover:shadow-md transition-all"
            >
              <CardContent className="flex flex-col items-center py-6 space-y-3">
                <Icon className="h-8 w-8 text-blue-600" />
                <span className="font-medium">{c.label}</span>
              </CardContent>
            </Card>
          );
        })}
      </div>
    </div>
  );
}
