"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import CategorySelector from "./components/CategorySelector";
import SubQueryForm from "./components/SubQueryForm";
import QueryPreview from "./components/QueryPreview";
import ResultView from "./components/ResultView";
import { sendQuery } from "../api/backend";

export default function QueryBuilderPage() {
  const [category, setCategory] = useState<string | null>(null);
  const [subQueries, setSubQueries] = useState<any[]>([]);
  const [finalQuery, setFinalQuery] = useState("");
  const [result, setResult] = useState<any>(null);

  const handleSubmit = async () => {
    const response = await sendQuery(finalQuery, 4);
    setResult(response.data);
  };

  return (
    <div className="p-10 max-w-6xl mx-auto space-y-10">
      <Card className="shadow-lg border border-zinc-200">
        <CardHeader>
          <CardTitle className="text-2xl font-bold tracking-tight">
            RoadMarshal AI â€” Query Builder
          </CardTitle>
        </CardHeader>

        <CardContent className="space-y-8">
          {!result && (
            <>
              {/* STEP 1: Select Category */}
              <CategorySelector setCategory={setCategory} />

              {/* STEP 2: Sub Query Form */}
              {category && (
                <SubQueryForm
                  category={category}
                  subQueries={subQueries}
                  setSubQueries={setSubQueries}
                  setFinalQuery={setFinalQuery}
                />
              )}

              {/* STEP 3: Final Query Preview */}
              {finalQuery && (
                <QueryPreview query={finalQuery} onSubmit={handleSubmit} />
              )}
            </>
          )}

          {/* STEP 4: Show Results */}
          {result && <ResultView result={result} />}
        </CardContent>
      </Card>
    </div>
  );
}
